.select_menu_container {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 20px 0 16px 0;
}

.select_menu {
  background: var(--color-primary-black);
  outline: none;
  font-size: 30px;
  line-height: 1.2;
  font-weight: 500;
  color: var(--color-primary-white);
  cursor: pointer;
}

.drop_container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.drop_container svg {
  transition: transform 250ms ease-out;
}

.drop_button {
  border: none;
  border-radius: 8px;
  border-bottom: 1px solid transparent;
  background-color: transparent;

  font-size: 30px;
  font-weight: 500;
  line-height: 1.2;
  color: var(--color-primary-white);
  cursor: pointer;

  transition: border-bottom 250ms ease-out;
}

.drop_button:hover {
  border-bottom: 1px solid var(--color-primary-white);
}

.drop_menu {
  position: absolute;
  bottom: -20;
  left: 0;
  z-index: 4;

  animation: slide-top 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.drop_menu > button {
  padding: 16px 101px 16px 12px;
  border: none;
  border-radius: 12px;
  background: var(--color-primary-black-2);
  box-shadow: 0px 4px 14px 0px rgba(227, 255, 168, 0.2);

  font-size: 22px;
  line-height: 1.45;
  color: var(--color-primary-white);
  cursor: pointer;
}

.chart_container {
  display: flex;
  flex-wrap: wrap;
  row-gap: 27px;
  padding-bottom: 60px;
}

@media screen and (min-width: 834px) {
  .chart_container {
    row-gap: 40px;
  }
}

@media screen and (min-width: 1440px) {
  .chart_container {
    gap: 20px;
    padding-bottom: 40px;
  }
}

.calories_element {
  overflow-x: auto;
}

@media screen and (min-width: 834px) {
  .calories_element {
    overflow-x: visible;
  }
}

@media screen and (min-width: 1440px) {
  .calories_element {
    max-width: 780px;
  }
}

.water_element {
  overflow-x: auto;
}

@media screen and (min-width: 834px) {
  .water_element {
    overflow-x: visible;
  }
}

@media screen and (min-width: 1440px) {
  .water_element {
    max-width: 780px;
  }
}

.weight_element {
  overflow-x: auto;
}

@media screen and (min-width: 834px) {
  .weight_element {
    overflow-y: visible;
  }
}

@keyframes slide-top {
  0% {
    transform: translateY(30px);
  }

  100% {
    transform: translateY(0);
  }
}
