.header {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: var(--color-primary-black-2);
  z-index: 45;
}

.container {
  display: grid;
  grid-template-columns: minmax(110px, 1fr) minmax(110px, 3fr);
  align-items: center;
  width: 100%;
  height: 100px;
  margin-left: 10px;
  margin-right: 10px;
  max-width: 320px;
}

@media screen and (min-width: 834px) {
  .container {
    max-width: 834px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
  }
}
