.container {
  max-width: 800px;
  max-height: 500px;
}
.drag_element {
  position: absolute;
  top: 50px;
  left: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 86px;
  height: 76px;
  box-shadow: 0px 4px 14px 0px rgba(227, 255, 168, 0.2);
  border-radius: 8px;
  cursor: pointer;
  user-select: none;
}
