.container_sing_in {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 1440px) {
  .container_sing_in {
    align-items: start;
    justify-content: flex-end;
    height: 588px;
  }
}

.container_sing_in h1 {
  margin-top: 24px;
  font-size: 30px;
  line-height: 1.2;
  font-weight: 500;
  color: var(--color-primary-white);
  text-align: center;
}

@media screen and (min-width: 834px) {
  .container_sing_in h1 {
    margin-top: 60px;
  }
}

@media screen and (min-width: 1440px) {
  .container_sing_in h1 {
    margin-top: 0;
    text-align: left;
  }
}

.sign_in_desc {
  margin-top: 16px;
  font-size: 22px;
  line-height: 1.45;
  color: var(--color-primary-grey);
  text-align: center;
}

.sign_in_form {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 16px;
  width: 100vw;
  max-width: 280px;
  padding-top: 24px;
}

@media screen and (min-width: 834px) {
  .sign_in_form {
    max-width: 360px;
  }
}

@media screen and (min-width: 1440px) {
  .sign_in_form {
    align-items: flex-start;
    max-width: 192px;
  }
}

.sign_in_input {
  width: 100%;
  padding: 8px 10px;
  border: 1px solid var(--color-primary-green-lite);
  border-radius: 12px;
  background: var(--color-primary-black-2);
  color: var(--color-primary-grey);
  -webkit-text-fill-color: white;
}

.sign_in_input:-webkit-autofill,
.sign_in_input:-webkit-autofill:focus {
  box-shadow: 0 0 0 1000px var(--color-primary-black-2) inset;
  -webkit-text-fill-color: var(--color-primary-grey);
}

.sing_up_box {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
  margin-top: 80px;
}

@media screen and (min-width: 834px) {
  .sing_up_box {
    flex-direction: row;
    row-gap: 0;
    column-gap: 16px;
    margin-top: 238px;
  }
}

@media screen and (min-width: 1440px) {
  .sing_up_box {
    margin-top: 200px;
  }
}

.sing_up_box span {
  font-size: 14px;
  line-height: 1.42;
  color: var(--color-primary-grey);
}

.sing_up_box a {
  font-size: 14px;
  line-height: 1.42;
  font-weight: 500;
  color: var(--color-primary-white);
}

.sign_in_form label {
  width: 100%;
}

.error {
  color: #e74a3b;
  font-size: 12px;
  line-height: 1.33;
  text-align: left;
  font-style: italic;
}

.hidden {
  display: none;
}

.showPasswordBtn {
  position: absolute;
  appearance: none;
}

.passwordIcon {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 16px;
  height: 16px;

  cursor: pointer;
}

.passwordContainer {
  position: relative;
  width: 100%;
}
